@import './colors.scss';

.quotation {
  margin-top: 200px;
  width: 100%;
  font-family: "Lato", sans-serif;

  &_calculator {
    display: flex;
    flex-wrap: wrap;
    width: 100%;

    &_lowerconstruction, &_upperconstruction,&_highconstruction {
      width: 50%;
      padding: 30px;
      
      &_inputs {
        padding: 30px 0 ;
        display: flex;
        flex-direction: column;

        input, select {
          margin: 10px 0;
          padding: 10px;
          border: 1px solid transparent;
          background-color: #f7f7f7;
          border-radius: 5px;
        }

      }
    }

    &_summary {
      display: flex;
      justify-content: flex-end;
      align-items: center;
      height: 100px;
      width: 100%;
      padding-right: 30px;
      background-color: $ctaColor;
      color: white;
      font-size: 20px;
    }
  }

  &_photo {
    position: relative;
    background-image: url('../img/tttttjpgt.jpeg');
    background-size: 100vw auto;
    background-repeat: no-repeat;

    height: 70vw;

    img{
    width: 100%;
    }

    h2 {
      position: absolute;
      width: 100%;
      top: 0%;
      right: 50%;
      transform: translate(50%, -50%);
      font-size: 50px;
      color: $ctaColor;
      text-align: center;
    }

  }

    
}


@media screen and (max-width: 900px) {
  .quotation {

    &_calculator {
      &_lowerconstruction, &_upperconstruction,&_highconstruction {
        width: 100%;
      }
    }
    &_photo{
      h2 {
        font-size: 30px;
      }
    }
  }
}