@import "./colors.scss";

.realization {
    max-width: 1444px;
    margin: 40px auto;
    margin-top: 100px;

    &_box {
        display: flex;
        margin: 40px 0;

        &_info {
            display: flex;
            flex-direction: column;
            justify-content: center;
            width: 50%;
            padding: 40px;

            h2 {
                font-size: 30px;
                margin-bottom: 50px;
                color: $textPrimaryColor;
                font-weight: 400;
            }

            p {
                color: $textSecondaryColor;
                font-weight: 200;
                line-height: 2;
            }
        }

        &_image {
            width: 50%;
            padding: 0 40px;

            a {
                img {
                    border-radius: 5px;
                }
            }
        }

        &:nth-child(1) {
            flex-direction: row-reverse;
        }
    }


    img {
        width: 100%;
    }
}

@media screen and (max-width: 900px) {

    .realization {

        width: 100%;

        &_box {
            flex-direction: column;
            width: 100%;

            &_info {
                width: 100%;
                padding: 20px;
            }

            &_image {
                width: 100%;
                padding: 20px;
            }

            &:nth-child(2n) {
                flex-direction: column;
            }
        }

    }
}

.qtest {
    display: flex;
    width: 100%;
    height: 400px;
    box-shadow: 0 20px 100px rgba(0, 0, 0, 0.1);
    margin: 30px 0;
    overflow: hidden;

    &:nth-child(4) {
        flex-direction: row-reverse;
    }

    &_photo {
        width: 100%; /* or any custom size */
        height: 100%; 
        object-fit: contain;
    }

    &_info {
        display: flex;
        flex-direction: column;
        justify-content: center;
        align-items: flex-start;
        width: 50%;
        color: black;
        padding: 40px;
        background-color: #ffffff;
        overflow: hidden;

        p {
            color: $textSecondaryColor;
            font-weight: 200;
            line-height: 2;
        }

        h2 {
            font-size: 30px;
            margin-bottom: 50px;
            color: $textPrimaryColor;
            font-weight: 400;
        }
    }
}